/* pagination */
.pagination-wrap {
	margin-bottom: 100px;
}

.pagination-list:after {
	content: '';
	display: block;
	clear: both;
}

.pagination-list li {
	float: left;
	min-width: 31px;
	height: 31px;
	line-height: 31px;
	border: 1px solid #ccc;
	text-align: center;
	font-size: 12px;
}

.pagination-list li + li {
	border-left: 0;
}

.pagination-list li a {
	box-sizing: border-box;
	display: block;
	width: 100%;
	height: 100%;
	padding: 0 5px;
	position: relative;
	color: #0061a0;
	background-color: #fff;
}

.pagination-list li a.active {
	background-color: #0061a0;
	color: #fff;
}

.pagination-list li a span {
	display: block;
	width: 5px;
	height: 5px;
	border: 0px solid #ccc;
	border-width: 1px 1px 0 0;
	position: absolute;
	top: 50%;
}

.pagination-list li a span.page-before {
	transform: translate(-50%, -50%) rotate(-135deg);
	left: 50%;
}

.pagination-list li a span.page-before:last-child {
	left: 60%;
}

.pagination-list li a span.page-after {
	transform: translate(45%, -50%) rotate(45deg);
	right: 45%;
}

.pagination-list li a span.page-after:last-child {
	right: 55%;
}

body.fixed {
	overflow: hidden;
	position: fixed;
}

.bg-dimmed {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	opacity: 0.5;
	z-index: 50;
}

.layer-wrap {
	z-index: 60;
}

.layer-slide {
	opacity: 0;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	background-color: #fff;
	padding: 3rem;
	transform: translateY(120%);
	transition: transform .4s;
}

.layer-slide-close {
	cursor: pointer;
}

.layer-slide.active {
	transform: translateY(0);
}

.layer-popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: none;
	overflow: auto;
}

.layer-popup.active {
	display: block;
}

.selectbox {
	position: relative;
	display: block;
	height: 40px;
	line-height: 40px;
	border: 1px solid #ccc;
	box-sizing: border-box;
	background-color: #fff;
	overflow: hidden;
}

.selectbox:before, .selectbox:after {
	content: '';
	position: absolute;
	right: 20px;
	display: block;
	width: 0;
	height: 0;
	border-style: solid;
}

.selectbox:before {
	bottom: 50%;
	margin-bottom: 1px;
	border-width: 0 6px 8px 6px;
	border-color: transparent transparent #535353 transparent;
}

.selectbox:after {
	top: 50%;
	margin-top: 1px;
	border-width: 8px 6px 0 6px;
	border-color: #535353 transparent transparent transparent;
}

.selectbox select {
	width: 100%;
	height: 100%;
	padding-left: 20px;
	padding-right: 40px;
	color: #000;
	font-size: 13px;
	border: none;
	background-color: transparent;
	appearance: none;
}

.selectbox select::-ms-expand {
	display: none;
}

.selectbox-inline {
	display: inline-block;
	vertical-align: middle;
}

.input-inline {
	display: inline-block;
	vertical-align: middle;
}

.input-box {
	box-sizing: border-box;
	display: inline-block;
	width: 100%;
	vertical-align: middle;
}

.input-box textarea,
.input-box input {
	width: 100%;
	height: 40px;
	padding: 0 15px;
	border: 1px solid #ccc;
	box-sizing: border-box;
	font-size: 13px;
	border-radius: 2px;
	color: #717171;
}

.input-box textarea[readonly],
.input-box input[readonly] {
	background-color: #f9f9f9;
}

.input-box textarea {
	min-height: 100px;
	height: auto;
	padding-top: 10px;
	padding-bottom: 10px;
}

.input-group {
	display: table;
	width: 100%;
}

.input-group > * {
	vertical-align: middle;
}

.input-group > *.selectbox, .input-group > *.input-box {
	width: 100%;
	padding-right: 15px;
}

.input-group .btn {
	margin-left: 5px;
	padding-right: 20px;
	padding-left: 20px;
}

.input-upload {
	position: relative;
	display: block;
	padding-right: 110px;
}

.input-upload input {
	cursor: pointer;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;
	width: 100%;
	height: 100%;
	border: 0;
	opacity: 0;
}

.input-upload .input-box {
	font-size: 13px;
	display: block;
	height: 40px;
	padding: 0 15px;
	border: 1px solid #ccc;
	border-radius: 2px;
	box-sizing: border-box;
}

.input-upload .value {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	height: 40px;
	padding: 0 15px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.input-upload .btn {
	position: absolute;
	top: 0;
	right: 0;
	width: 96px;
	padding: 0;
}

.radio {
	margin-right: 30px;
}

.radio input[type="radio"] {
	position: absolute;
	width: 0;
	height: 0;
	opacity: 0;
}

.radio input[type="radio"] + label {
	position: relative;
	padding-left: 30px;
	letter-spacing: -.75px;
	color: #666;
}

.radio input[type="radio"] + label:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	display: inline-block;
	width: 20px;
	height: 20px;
	border: 1px solid #cdcdcd;
	border-radius: 50%;
	box-sizing: border-box;
}

.radio input[type="radio"] + label:after {
	content: '';
	position: absolute;
	top: 6px;
	left: 6px;
	display: none;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background-color: #007abc;
	opacity: 0;
}

.radio input[type="radio"]:checked + label:after {
	content: '';
	display: inline-block;
	opacity: 1;
}

.checkbox {
	margin-right: 30px;
	text-align: left;
}

.checkbox input[type="checkbox"] {
	position: absolute;
	width: 0;
	height: 0;
	opacity: 0;
}

.checkbox input[type="checkbox"] + label {
	position: relative;
	padding-left: 30px;
	letter-spacing: -.75px;
	color: #666;
}

.checkbox input[type="checkbox"] + label:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	display: inline-block;
	width: 20px;
	height: 20px;
	border: 1px solid #cdcdcd;
	border-radius: 1px;
	box-sizing: border-box;
	background-color: #fff;
}

.checkbox input[type="checkbox"] + label:after {
	content: '';
	position: absolute;
	top: 6px;
	left: 6px;
	display: none;
	width: 0;
	height: 0;
	border-radius: 1px;
	background-color: #007abc;
	opacity: 0;
	transition: opacity .5s ease;
}

.checkbox input[type="checkbox"]:checked + label:after {
	content: '';
	display: inline-block;
	width: 8px;
	height: 8px;
	opacity: 1;
}

.addfile-delete label {
	vertical-align: middle;
}

